    }
  }

  .section-7 .section--cards .item .card .card--img {
    height: 150px;
  }

  .section-7 .section--cards .item .card .card--content .card--title {
    font-size: 18px;
    line-height: 28px;
  }
  .section-7 .section--cards .item .card .card--content .author {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 8px;
  }

  .section-7 .section--cards .item .card .card--content .date {
