    line-height: 38px;
    margin-bottom: 30px;
  }

  .footer .contact--banner .banner--form input {
    height: 50px;
    font-size: 18px;
    &::placeholder {
      font-size: 18px;
    }
  }

  .footer .contact--banner .banner--form button {
    font-size: 18px;
    padding: 11px 20px;
  }
}

@media screen and (max-width: 991px) {
  .header--bottom {
    padding: 20px 0;
  }
  .main-container {
    padding: 0 20px;
  }
  .header--nav {
    position: fixed;
    width: 300px;
    top: 0;
    right: -300px;
    height: 100%;
    background-color: #1c1c33;
    padding: 70px 10px 0;
    z-index: 99999;
    transition: 0.3s;
    .header--list {
      flex-direction: column;
      .header--list__item:not(:last-child) {
        margin: 0 0 25px;
      }
    }
    .uil-times {
      display: block !important;
      position: absolute;
      top: 20px;
      left: 20px;
      font-size: 28px;
      color: #f5f5f5;
      cursor: pointer;
