    flex: 0 0 100%;
    max-width: 100%;
  }
  .section-1 .section--banner__left .title {
    text-align: center;
  }

  .section-1 .section--banner__left .subheader {
    text-align: center;
  }

  .section-1 .section--banner__right .banner--img {
    text-align: center;
    img {
      width: 80%;
    }
  }

  .section-3 .main-container .section--banner {
    flex-wrap: wrap-reverse;
  }

  .section-3 .main-container .section--banner .banner--content {
    padding: 0 20px 0 40px;
    margin-bottom: 40px;
    flex: 0 0 100%;
    max-width: 100%;
  }

  .section-3 .main-container .section--banner .banner--img {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .section-7 {
    padding: 100px 0;
  }

  .footer {
    padding-top: 50px;
  }

  .footer .contact--banner {
    top: auto;
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 767px) {
  .header--top .header--text {
    font-size: 14px;
    line-height: 22px;
  }

  .section-2 .section--cards .item {
    margin-bottom: 30px;
    flex: 0 0 50%;
    max-width: 50%;
  }

