    flex: 0 0 100%;
    max-width: 100%;
    padding: 0;
    margin-bottom: 50px;
  }
  .section-6 .section--banner__left .banner--img {
    text-align: center;
    img {
      width: 80%;
    }
  }

  .section-7 .section--cards .item {
    flex: 0 0 50%;
    max-width: 50%;
    margin-bottom: 30px;
  }

  .footer .contact--banner {
    padding: 50px;
  }

  .footer .contact--banner .banner--title {
